html {
    scroll-behavior: smooth;
}

nav {
    height: 172px !important;

    @media only screen and (max-width: 992px) {
        height: 100px !important;
    }

    background-color: white;
    padding: 0px !important;
    z-index: 9999;
    background-color: white;

    box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);
    -webkit-box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);
    -moz-box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);

    .navbar-top-container {
        height: 100px;
        background-color: white;
        align-items: center;
        justify-content: center;
        z-index: 99;

        @media screen and (max-width: 992px) {
            box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);
            -webkit-box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);
            -moz-box-shadow: -9px 27px 47px -49px rgba(0, 0, 0, 0.43);
        }

        .navbar-logo-container {
            padding: 0px;
            margin: 0px;
            margin: auto;
            cursor: pointer;
            background-color: white;

            .navbar-brand {
                font-family: inherit;
                color: black;
                font-weight: bolder;
                font-size: 37px;
                letter-spacing: -2px;
            }

            .logo {
                height: 75px;

                @media only screen and (max-width: 992px) {
                    height: 65px !important;
                }
            }
        }
    }

    .seperator {
        @media screen and (min-width: 992px) {
            border-top: 0.5px solid lightgrey;
        }

        @media screen and (max-width: 992px) {
            border-top: 0.1px solid rgb(248, 248, 248);
        }

        z-index: 9999;
        width: 100%;
    }

    .navbar-bottom-container {
        height: 70px;
        flex-direction: row;
        background-color: white;
        z-index: 9999;
        background-color: white;

        @media only screen and (max-width: 992px) {
            display: none !important;
        }

        .navbar-bottom-flex {
            height: 100%;
            justify-content: center;
            align-items: center;
            display: flex;
            flex: 1;
            color: black;

            &:hover {
                color: white;
                background-color: black;
                cursor: pointer;
            }

            .navbar-bottom-link {
                margin-left: 7.5px;
                text-align: center;
                color: inherit;
                font-size: 20px;
                text-decoration: none;
            }
        }
    }

    .navbar-menu-toggler {
        position: absolute;
        width: 50px;
        height: 50px;
        cursor: pointer;

        .navbar-menu-toggler-icon-container {
            margin: auto;
            height: 25px;
            width: 25px;
            align-items: center;
            flex: 1;
        }

        @media only screen and (min-width: 992px) {
            display: none !important;
        }
    }

    .navbar-bottom-container-responsive {
        padding: 0px 0px;
        position: absolute;
        top: 100.5px;
        width: 100vw;
        background-color: white;
        z-index: 98 !important;
        border-bottom: 1px solid lightgray;
        padding-bottom: 10px;
        box-shadow: -9px 27px 108px -49px rgba(0, 0, 0, 0.5);
        -webkit-box-shadow: -9px 27px 108px -49px rgba(0, 0, 0, 0.5);
        -moz-box-shadow: -9px 27px 108px -49px rgba(0, 0, 0, 0.5);

        @media only screen and (min-width: 992px) {
            display: none !important;
        }

        .navbar-bottom-responsive-flex {
            width: 100%;
            justify-content: center;
            align-items: center;
            height: 55px;
            display: flex;
            padding: 0px 20px;

            .navbar-bottom-responsive-link {
                margin: auto;
                margin-left: 20px;
                text-align: center;
                color: inherit;
                font-size: 20px;
                text-decoration: none;

                &:hover {
                    color: #999C9B;
                }
            }
        }
    }

    .navbar-fixed-wrapper {
        background-color: white;
        position: fixed;
        width: 100vw !important;
        z-index: 9999;
        height: 75px;
        top: 0px;
        left: 0px;
        box-shadow: -3px 7px 49px -7px rgba(0, 0, 0, 0.44);
        -webkit-box-shadow: -3px 7px 49px -7px rgba(0, 0, 0, 0.44);
        -moz-box-shadow: -3px 7px 49px -7px rgba(0, 0, 0, 0.44);
        visibility: hidden;

        @media screen and (max-width:1200px) {
            display: none !important;
        }

        .navbar-bottom-container-fixed {
            height: 75px;
            flex-direction: row;
            position: fixed;
            left: 50%;
            top: 0px;
            background-color: white;
            transform: translateX(-50%);
            display: flex;

            .logo-container {
                width: 200px;
                padding-left: 50px;

                .logo {
                    margin-top: 10px;
                    height: 55px;
                }
            }

            @media only screen and (max-width: 992px) {
                display: none !important;
            }

            .navbar-bottom-flex {
                height: 100%;
                justify-content: center;
                align-items: center;
                display: flex;
                flex: 1;
                color: black;

                &:hover {
                    color: white;
                    background-color: black;
                    cursor: pointer;
                }

                .navbar-bottom-link {
                    margin-left: 7.5px;
                    text-align: center;
                    color: inherit;
                    font-size: 20px;
                    text-decoration: none;
                }
            }
        }
    }

    .active {
        color: white !important;
        background-color: black;
        cursor: pointer;
        opacity: 0.5;
    }

}