.bannerVideoCoverage {
    position: fixed;
    bottom: 30px;
    left: 30px;
    width: 403px;
    height: 228px;
    margin: 0px;
    padding: 0px;
    border: none;
    /* box-shadow: -3px 1px 21px -6px rgba(255, 255, 255, 0.75);
-webkit-box-shadow: -3px 1px 21px -6px rgba(255, 255, 255, 0.75);
-moz-box-shadow: -3px 1px 21px -6px rgba(255, 255, 255, 0.75); */
    .bottomVideo {
        background-color: black;
        position: fixed;
        width: 400px;
        height: 225px;
        z-index: 8000 !important;
        background-image: url("../../assets/gif/tv.gif");
    }
    .overlay-video {
        position: fixed;
        left: -21px;
        width: 442px;
        bottom: -23px;
        height: 265px;
        background-size: cover;
        background-repeat: none !important;
    }

    #closeVideo:hover {
        animation: shake;
        animation-duration: 2s;
    }
    iframe {
        z-index: 999;
    }
    video[poster] {
        border: inherit;
        object-fit: cover;
        width: 400px;
        height: calc(300px * 1.71428);
    }

    .vjs-poster {
        background-size: cover;
        background-position: inherit;
    }
}
